'use client';

import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

export default function SentimentChart({ data }) {
  if (!data || data.length === 0) return <p>No chart data available</p>;

  const chartData = {
    labels: data.map(q => q.quarter.replace('_', ' ')),
    datasets: [
      {
        label: 'Management Sentiment',
        data: data.map(q => q.management.score),
        borderColor: 'rgb(53, 162, 235)',
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
        tension: 0.3
      },
      {
        label: 'Q&A Sentiment',
        data: data.map(q => q.qa.score),
        borderColor: 'rgb(255, 99, 132)',
        backgroundColor: 'rgba(255, 99, 132, 0.5)',
        tension: 0.3
      }
    ]
  };

  const options = {
    responsive: true,
    plugins: {
      legend: { position: 'top' },
      title: {
        display: true,
        text: 'Sentiment Scores by Quarter'
      }
    },
    scales: {
      y: {
        min: -1,
        max: 1,
        ticks: {
          stepSize: 0.5
        }
      }
    }
  };

  return <Line data={chartData} options={options} />;
}